"use client";
import { Button } from "@/components/ui/button";
import { useState } from "react";
import {  Field, Input, Text } from "@chakra-ui/react";

export default function Home() {
  const [searchTerm, setSearchTerm ] = useState<string>("");

  async function fetchAuthors(searchTerm: string) {
    console.log('submitted');
    const res = await fetch(`https://37ey6hgnp4.execute-api.eu-west-2.amazonaws.com/?author=${encodeURIComponent(searchTerm)}`);
    const data = await res.json();
    console.log(data);
    // Optionally, setAuthors(data.docs || []);
    return data;
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    await fetchAuthors(searchTerm);
  };

  const [authors, setAuthors] = useState<any[]>([])
  const [books, setBooks] = useState<any[]>([])

  return (
    <main className="flex justify-center items-center min-h-screen">
      <div className="text-center grid grid-cols-2 gap-8 w-full max-w-4xl p-3"> 
          <form onSubmit={handleSubmit} className="content-center flex flex-col gap-4">
            <Field.Root>
              <Field.Label className={'text-cyan-300 text-center'}>
                <Text fontWeight="normal" textStyle="md">
                  Search for a Book
                </Text>
              </Field.Label>
              <Input
                name="name"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </Field.Root>
            <Button size="xs">Search</Button>
          </form> 
        <div className="border-l-2 border-cyan-500 pl-8">
          <h2 className="font-bold text-cyan-500">Authors</h2>
          {

          }
        </div>
      </div>
    </main>
  );
}
